<template>
    <div v-loading="loading">
        <el-form ref="form" :model="form" :rules="rules" status-icon>
            <el-form-item class="a-ui" prop="vacancyField" label=" ">
                <el-input placeholder=" , ..." v-model="form.vacancyField"></el-input>
            </el-form-item>
            <el-form-item class="a-ui" prop="vacancyName" label=" ">
                <el-input placeholder=" , ..." v-model="form.vacancyName"></el-input>
            </el-form-item>
            <el-form-item class="a-ui" prop="description" label="">
                <wysiwyg class="mt-5" v-model="form.description" placeholder="  "/>
            </el-form-item>
            <el-form-item class="a-ui" prop="maxSalary" label="">
                <div style="width: 100%; overflow: auto">
                    <el-input-number :min="form.minSalary" :step="500" v-model="form.maxSalary"></el-input-number>
                </div>
            </el-form-item>
            <div class="text-right">
                <el-button @click="clearForm('form')"></el-button>
                <el-button type="primary" @click="submitForm('form')"></el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
    import { CREATE_VACANCY } from '../../store/mutation-types';
    import ElFormItem from "../../../node_modules/element-ui/packages/form/src/form-item";
    import ElInputNumber from "../../../node_modules/element-ui/packages/input-number/src/input-number";
    import {VACANCIES} from "../../store/types/vacancies";

    export default {
        components: {
            ElInputNumber,
            ElFormItem},
        name: 'new-vacancy',
        data() {
            return {
                loading: false,
                types: [
                    {label: ' ', value: ' '},
                    {label: ' ', value: ' '},
                    {label: ' ', value: ' '},
                    {label: '', value: ''},
                    {label: '', value: ''}
                ],
                form: {
                    vacancyField: '',
                    vacancyName: '',
                    description: '',
                    demands: [],
                    type: [],
                    minSalary: 0,
                    maxSalary: 100000,
                },
                rules: {
                    vacancyField: [
                        { required: true, message: '  ', trigger: 'blur' },
                    ],
                    vacancyName: [
                        { required: true, message: '  ', trigger: 'blur' },
                    ],
                    description: [
                        { min: 40, message: '     40 ', trigger: 'blur' }
                    ],
                }
            }
        },
        computed: {
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.loading = true
                        if (this.form.description === '') {
                            delete this.form.description
                        }
                        this.$store.dispatch(VACANCIES + CREATE_VACANCY , this.form).then(() => {
                            const h = this.$createElement;
                            this.$message({
                                message: h('p', null, [
                                    h('span', null, ' '),
                                    h('i', { style: 'color: teal' }, `"${this.form.vacancyName}"`),
                                    h('span', null, '  '),
                                ]),
                                type: 'success'
                            });
                            this.loading = false
                            setTimeout(() => {
                                this.$emit('success', true);
                            }, 1000)

                        })
                    } else {
                        this.$alert('   ', {
                            confirmButtonText: 'OK',
                        })
                            .then()
                        return false
                    }
                })
            },
            clearForm(formName) {
                this.$emit('cancel', true);
                this.$refs[formName].resetFields()
            }
        }
    }
</script>
